@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,900|Source+Sans+Pro:300,900&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}


/* Custom Properties, update these for your own design */

:root {
    --ff-primary: 'Source Sans Pro', sans-serif;
    --ff-secondary: 'Source Code Pro', monospace;
    --fw-reg: 300;
    --fw-bold: 900;
    --clr-light: #fff;
    --clr-dark: #000;
    --clr-accent: #303030;
    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.25rem;
    --fs-body: 1rem;
    --bs: 0.25em 0.25em 0.75em rgba(0, 0, 0, .25), 0.125em 0.125em 0.25em rgba(0, 0, 0, .15);
}

@media (min-width: 800px) {
     :root {
        --fs-h1: 4.5rem;
        --fs-h2: 3.75rem;
        --fs-h3: 1.5rem;
        --fs-body: 1.125rem;
    }
}


/* General styles */

html {
    scroll-behavior: smooth;
}

body {
    background: #fff;
    background: var(--clr-light);
    color: #000;
    color: var(--clr-dark);
    margin: 0;
    font-family: 'Source Sans Pro', sans-serif;
    font-family: var(--ff-primary);
    font-size: 1rem;
    font-size: var(--fs-body);
    line-height: 1.6;
}

section {
    padding: 5em 2em;
}

img {
    display: block;
    max-width: 100%;
}

strong {
    font-weight: 900;
    font-weight: var(--fw-bold)
}

:focus {
    outline: 3px solid #303030;
    outline: 3px solid var(--clr-accent);
    outline-offset: 3px;
}


/* Typography */

h1,
h2,
h3 {
    line-height: 1;
    margin: 0;
}

h1 {
    font-size: 3rem;
    font-size: var(--fs-h1)
}

h2 {
    font-size: 2.25rem;
    font-size: var(--fs-h2)
}

h3 {
    font-size: 1.25rem;
    font-size: var(--fs-h3)
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
    position: fixed;
    right: 0;
    left: 0;
    background-color: var(--clr-light);
    z-index: 100;
    box-shadow: 0px 12px 11px -3px rgba(48, 48, 48, 1);
}

.logo {
    max-width: 200px;
}

.nav {
    position: fixed;
    background: var(--clr-dark);
    color: var(--clr-light);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.nav__list {
    list-style: none;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0;
    padding: 0;
}

.nav__link {
    color: inherit;
    font-weight: var(--fw-bold);
    font-size: var(--fs-h2);
    text-decoration: none;
}

.nav__link:hover {
    color: #afafaf;
}

.nav-toggle {
    padding: .5em;
    background: transparent;
    border: 0;
    cursor: pointer;
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 1000;
}

.hamburger {
    display: block;
    position: relative;
}

.hamburger,
.hamburger::before,
.hamburger::after {
    background: var(--clr-dark);
    width: 2em;
    height: 3px;
    border-radius: 1em;
    transition: transform 250ms ease-in-out;
}

.hamburger::before,
.hamburger::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
}

.hamburger::before {
    top: 6px;
}

.hamburger::after {
    bottom: 6px;
}

.nav-open+.nav {
    transform: translateX(0%);
}

.nav-open .nav-toggle {
    position: fixed;
}

.nav-open .hamburger {
    transform: rotate(.625turn);
    background: var(--clr-light);
}

.nav-open .hamburger::before {
    transform: rotate(90deg) translateX(-6px);
    background: var(--clr-light);
}

.nav-open .hamburger::after {
    opacity: 0;
}

@media(min-width: 750px) {
    header {
        max-width: 1200px;
        padding-left: 2em;
        padding-right: 2em;
        padding-bottom: 0;
        margin: 0 auto;
        opacity: 0.9;
    }
    .nav-toggle {
        display: none;
    }
    .nav {
        position: relative;
        background-color: var(--clr-light);
        color: var(--clr-dark);
        transform: none;
        width: 60%;
    }
    .nav__list {
        display: flex;
        flex-direction: row;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
    }
    .nav__link {
        font-size: var(--fs-nav);
        display: inline-block;
    }
}
.intro {
    position: relative;
    padding-top: 40vh;
    padding-bottom: 20vh;
}

.section__title--intro {
    font-weight: var(--fw-reg);
    text-align: right;
}

.section__title--intro strong {
    display: block;
}

.section__subtitle--intro {
    color: var(--clr-light);
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .intro {
        display: grid;
        width: -webkit-min-content;
        width: -moz-min-content;
        width: min-content;
        margin: 0 auto;
        grid-column-gap: 1em;
        grid-template-areas: "img title" "img subtitle";
        grid-template-columns: -webkit-min-content -webkit-max-content;
        grid-template-columns: min-content max-content;
    }
    .intro__img {
        grid-area: img;
        min-width: 350px;
        position: relative;
        z-index: 2;
    }
    .section__subtitle--intro {
        align-self: start;
        grid-column: -1 / 1;
        grid-row: 2;
        text-align: right;
        position: relative;
        left: -1.5em;
        width: calc(100% + 1.5em);
    }
}
.about-me {
    max-width: 1000px;
    margin: 0 auto;
}

.section__subtitle--about {
    color: white;
}

.about-me__img {
    box-shadow: var(--bs);
}

@media (min-width: 600px) {
    .about-me {
        display: grid;
        grid-template-columns: 1fr 200px;
        grid-template-areas: "title img" "subtitle img" "text img";
        grid-column-gap: 2em;
    }
    .section__title--about {
        grid-area: title;
    }
    .section__subtitle--about {
        grid-column: 1 / -1;
        grid-row: 2;
        position: relative;
        left: -1em;
        width: calc(100% + 2em);
        padding-left: 1em;
        padding-right: calc(200px + 4em);
    }
    .about-me__img {
        grid-area: img;
        position: relative;
        z-index: 2;
    }
}
.my-work {
    text-align: center;
    max-width: 1000px;
    margin: 0 auto;
}

.portfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.ba {
    border-style: solid;
    border-width: 2px;
}

.b--black-10 {
    border-color: rgba(0, 0, 0, .1);
}

.br2 {
    border-radius: .25rem;
}

.mw5 {
    max-width: 16rem;
}

.w-100 {
    width: 100%;
}

.dark-gray {
    color: #333;
}

.mv4 {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.center {
    margin-right: auto;
    margin-left: auto;
}

.portfolio__item {
    box-shadow: var(--bs);
    max-width: 95%;
    min-height: 320px;
    display: flex;
    flex-direction: column;
}

.portfolio__item p {
    text-transform: capitalize;
}

.portfolio__item:focus {
    position: relative;
    z-index: 2;
    transform: scale(1.2);
    opacity: .5;
}

.portfolio__links {
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin: 0 auto 1em;
}

.portfolio__links a {
    background-color: var(--clr-accent);
    color: var(--clr-light);
    font-weight: bold;
    border-radius: 0.5em;
    padding: 0.5em;
    text-decoration: none;
}

.techlist {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 90%;
    margin: auto;
}

.techlist__item {
    margin-left: 0.5em;
}
.footer {
    background: var(--clr-accent);
    color: var(--clr-light);
    text-align: center;
    padding: 2.5em 0;
    font-size: var(--fs-h3);
}

.footer a {
    color: inherit;
    text-decoration: none;
}

.footer__link {
    font-weight: var(--fw-bold);
}

.footer__link:hover,
.social-list__link:hover {
    opacity: .7;
}

.footer__link:hover {
    text-decoration: underline;
}

.social-list {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 2em 0 0;
    padding: 0;
}

.social-list__item {
    margin: 0 .5em;
}

.social-list__link {
    padding: 1em;
    position: relative;
    z-index: 10;
}

object {
    position: relative;
    z-index: -1;
}
.section__title {
    margin-bottom: .25em;
}

.section__subtitle {
    margin: 0;
    font-size: var(--fs-h3);
}

.section__subtitle--intro,
.section__subtitle--about {
    background: var(--clr-accent);
    padding: .25em 1em;
    font-family: var(--ff-secondary);
    margin-bottom: 1em;
}

.section__subtitle--work {
    color: var(--clr-accent);
    font-weight: var(--fw-bold);
    margin-bottom: 2em;
}
